<template>
	<view class="addProject">
		<view class="info">
			<u-form :model="form" ref="uForm" label-width="150">
				<u-form-item label="主机ID" prop="name"><u-input v-model="form.name" /></u-form-item>
				<u-form-item label="安装位置" prop="address" right-icon="map-fill"><u-input @click="s" v-model="form.address" /></u-form-item>
				<u-form-item label="设备类型" prop="fireMan">
					<u-input @click="show = true" v-model="form.fireMan" type="select" />
					<u-select v-model="show" :list="list" @confirm="confirm"></u-select>
					<!-- <u-button @click="show = true">打开</u-button> -->
				</u-form-item>

				<u-form-item label="主机名称" prop="intro"><u-input v-model="form.intro" /></u-form-item>
				<u-form-item label="应用场所" prop="intro">
					<u-input @click="show = true" v-model="form.fireMan" type="select" />
					<u-select v-model="show" :list="list" @confirm="confirm"></u-select>
				</u-form-item>
				<u-form-item label="短信推送" prop="intro">
					<u-radio-group v-model="value">
						<u-radio v-for="(item, index) in list2" :key="index" :name="item.name" :disabled="item.disabled">{{ item.name }}</u-radio>
					</u-radio-group>
				</u-form-item>
				<u-form-item label="备注" prop="intro"><u-input v-model="form.intro" /></u-form-item>
			</u-form>
			<u-button @click="submit">提交</u-button>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			show: false,
			list: [
				{
					value: '1',
					label: '江'
				},
				{
					value: '2',
					label: '湖'
				}
			],
			form: {
				name: '',
				intro: '',
				address: '',
				fireMan: ''
			},
			rules: {
				name: [
					{
						required: true,
						message: '请输入姓名',
						// 可以单个或者同时写两个触发验证方式
						trigger: ['change', 'blur']
					}
				]
				// fireMan: [
				// 	{
				// 		min: 5,
				// 		message: '简介不能少于5个字',
				// 		trigger: ['change', 'blur']
				// 	}
				// ]
			},
			list2: [
				{
					name: 'apple',
					disabled: false
				},
				{
					name: 'banner',
					disabled: false
				},
				{
					name: 'orange',
					disabled: false
				}
			],
			// u-radio-group的v-model绑定的值如果设置为某个radio的name，就会被默认选中
			value: 'orange'
		};
	},
	methods: {
		confirm(e, data) {
			console.log(e, data);
			this.form.fireMan = e[0].label;
		},
		s() {
			let that = this;
			console.log(12123);
			uni.chooseLocation({
				success: function(res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
					that.form.address = res.address;
					console.log(that.form.address, 22);
				}
			});
		},
		submit() {
			this.$refs.uForm.validate(valid => {
				if (valid) {
					console.log('验证通过');
				} else {
					console.log('验证失败');
				}
			});
		}
	},
	// 必须要在onReady生命周期，因为onLoad生命周期组件可能尚未创建完毕
	onReady() {
		this.$refs.uForm.setRules(this.rules);
	}
};
</script>
<style lang="scss" scoped>
.addProject {
	padding: 10px;
	
	
}
</style>
